@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);

body{
    margin: 0 auto;
    font-family: 'Montserrat', sans-serif;
}
.header-btn {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: right;
	border-radius: 4px;
	cursor: pointer;
	opacity: 0.9;
	/* background: rgb(217, 85, 80); */
	background: none rgba(255, 255, 255, 0.2);
	box-shadow: none;
	margin-left: 10px;
	font-size: 13px;
	padding: 8px 12px;
	min-width: 70px;
	border: none;
	color: white;
}
.header-btn img {
	width: 15px;
	height: 14px;
	margin-right: 5px;
}

.header-wrap {
	padding: 15px 12px;
	max-width: 620px;
	margin: auto;
}

.header-content {
	background-color: inherit;
	width: 620px;
	height: 60px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header-title {
	display: inline-block;
	color: white;
	margin: 0px;
	padding: 10px 0px;
	font-size: 20px;
}

.check {
	width: 20px;
	margin-right: 4px;
}
.btn-wrap {
	display: flex;
	align-items: center;
}

.settings-wrap {
	color: black;
	text-align: left;
}
.modal-block-wrap {
	background-color: white;
	border-radius: 8px;
	max-width: 400px;
	width: 95%;
	min-width: 400px;
}

.settings-wrap h2 {
	display: inline-block;
	color: #bcbcbc;
	text-align: center;
	margin-left: 90px;
	margin-right: 100px;
}

.settings-wrap img {
	opacity: 0.2;
	height: 18px;
}
.modal-settings div {
	display: inline-block;
	margin-inline: 5px;
	padding-left: 30px;
}
.modal-settings input {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	padding: 10px;
	position: static;
	width: 40px;
	height: 20px;
	left: 0px;
	top: 23px;
	margin-bottom: 23px;
	background: #efefef;
	border-radius: 6px;
	border: none;
	outline: none;
}
.modal-settings h3 {
	font-size: 12px;
	color: #bcbcbc;
}
.settings-wrap p {
	margin-left: 35px;
	font-weight: bold;
	position: static;
	font-style: normal;
	font-weight: bold;
	font-size: 12px;
	line-height: 17px;
}
hr {
	background-color: #d6d6d6;
	width: 340px;
}
.auto1 {
	padding: 20px 0px;
	min-height: 30px;
	display: flex;
	flex-direction: column;
	margin: 0px 40px;
}
.auto2 {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.auto3 {
	display: inline-block;
}
.auto_span {
	font-weight: bold;

	font-size: 18px;
}
.auto_btn {
	cursor: pointer;
	width: 55px;
	height: 32px;
	border-radius: 50px;
	background-color: rgb(204, 204, 204);
	position: relative;
	display: inline-flex;
	align-items: center;
}
.auto_circle {
	position: absolute;
	left: 2px;
	width: 26px;
	height: 26px;
	border-radius: 50px;
	background-color: white;
	box-shadow: rgb(0 0 0 / 30%) 0px 1px 1px;
}
.long_break {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.long_break2 {
	display: inline-block;
	font-size: 18px;
}
.long_span {
	font-weight: bold;
	display: flex;
	align-items: center;
}
.long_input {
	border-radius: 4px;
	background-color: rgb(239, 239, 239);
	border: none;
	outline: none;
	padding: 8px;
	position: static;
	width: 35px;
	height: 15px;
	text-align: center;
}
.ok_btn {
	padding: 14px 20px;
	text-align: right;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 8px;
}
.ok_btn button {
	align-items: center;
	justify-content: center;
	text-align: center;
	border-radius: 4px;
	cursor: pointer;
	box-shadow: rgb(0 0 0 / 20%) 0px 2px 2px;
	color: white;
	opacity: 0.9;
	font-size: 14px;
	padding: 8px 12px;
	min-width: 60px;
	background-color: rgb(34, 34, 34);
	border: 2px solid rgb(34, 34, 34);
	display: inline-block;
	margin-right: 13px;
}
.auto_btnon {
	cursor: pointer;
	width: 60px;
	height: 32px;
	border-radius: 50px;
	position: relative;
	display: inline-flex;
	align-items: center;
	background-color: rgba(132, 199, 51, 0.8);
}
.auto_circleon {
	position: absolute;
	left: 28px;
	width: 28px;
	height: 28px;
	border-radius: 50px;
	background-color: white;
	box-shadow: rgb(0 0 0 / 30%) 0px 1px 1px;
}

.modal-wrap {
	background-color: rgba(0, 0, 0, 0.4);
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100vh;
	z-index: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden scroll;
	padding: 8px 0px;
	box-sizing: border-box;
}

.modal-block {
	background-color: white;
	border-radius: 8px;
	max-width: 400px;
	width: 95%;
	min-width: 400px;
}

.content-wrap {
	padding: 0px 12px;
	max-width: 620px;
	margin: 0 auto;
}

.main-content {
	color: rgb(255, 255, 255);
	text-align: center;
}

.contain-pomodoro {
	background: rgb(219, 82, 77);
	transition: all 0.4s ease;
}

.contain-longbreak {
	background: #468e91;
	transition: all 0.4s ease;
}

.contain-shortbreak {
	background: #437ea8;
	transition: all 0.4s ease;
}

.contain-pomodoro .start-btn {
	color: rgb(219, 82, 77);
	transition: all 0.4s ease;
}

.contain-longbreak .start-btn {
	color: #468e91;
	transition: all 0.4s ease;
}

.contain-shortbreak .start-btn {
	color: #437ea8;
	transition: all 0.4s ease;
}

.contain-pomodoro .pomodoro {
	background: rgb(0, 0, 0, 0.15);
}

.contain-shortbreak .shortbreak {
	background: rgb(0, 0, 0, 0.15);
}

.contain-longbreak .longbreak {
	background: rgb(0, 0, 0, 0.15);
}

.content-wrap {
	margin: auto;
	padding-bottom: 20px;
}
.content-block {
	margin-left: 80px;
	background-color: rgba(255, 255, 255, 0.1);
	padding: 20px 0px 30px;
	border-radius: 6px;
	margin-bottom: 20px;
	width: 460px;
	height: 326px;
}
.content-btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	padding-top: 30px;
}
.pomodoro-timer {
	font-size: 120px;
	font-weight: 700;
	margin-top: 20px;
	height: 146px;
}
.pomodoro-start {
	position: relative;
}

.pomodoro-skip {
	width: calc((100% - 200px) / 2 + 5%);
	position: absolute;
	right: 0px;
	bottom: -1px;
	height: 54px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.pomodoro-skip button {
	cursor: pointer;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	background: none;
	border: none;
	width: 28px;
	height: 28px;
	align-items: center;
	justify-content: center;
	bottom: 9px;
	right: -50px;
	pointer-events: none;
	transition: all 0.2s ease-out 0s;
}

.pomodoro-skip img {
	width: 17px;
	height: 21px;
	opacity: 0.9;
}

#content-btn {
	border: none;
	color: white;
	border-radius: 4px;
	font-size: 16px;
	padding: 2px 12px;
	height: 28px;
	cursor: pointer;
	box-shadow: none;
	font-weight: 300;
	transition: color 0.5s ease-in-out 0s;
}
button {
	background: none;
}

.start-btn {
	cursor: pointer;
	border: none;
	margin: 20px 0px 0px;
	padding: 0px 12px;
	border-radius: 4px;
	font-size: 22px;
	height: 55px;
	color: rgb(219, 82, 77);
	font-weight: bold;
	width: 200px;
	background-color: white;
	transition: color 0.5s ease-in-out 0s;
}
.start-btn:active {
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
.confirm-wrap {
	height: 195px;
	width: 485px;
	border-radius: 6px;
	padding: 40px;
	color: black;
	background: white;
	margin-left: -80px;
}
.confirm-inner-wrap p {
	display: flex;
	justify-content: left;
	text-align: left;
}

p{
    font-family: 'Montserrat', sans-serif;
}

.confirm-buttons button {
	outline: none;
	border: 1px solid rgb(75, 72, 72);
	border-radius: 6px;
}
.confirm-buttons {
	text-align: right;
}
.confirm-buttons div {
	display: inline-block;
	padding-left: 10px;
}
.btnOk {
	width: 63px;
	height: 37px;
	background-color: #373737;
	color: white;
}
.btnNo {
	width: 83px;
	height: 37px;
	color: 	#373737;
}

.progress-line{
    background-color: rgba(0,0,0,0.1);
    height: 1px;
    margin-bottom: 40px;
    width: 610px;
    margin-left: 12px;
}
.progress-fill{
    height: 3px;
    background-color: white;
    border-radius: 100px;
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    width: 0px; 
}
.footer {
	margin-top: 40px;
	border-top: 2px solid #efefef;
	text-align: center;
	padding-top: 30px;
	color: hsl(357, 12%, 42%);
	padding-bottom: 20px;
}
.footer_nav {
	margin-bottom: 14px;
}
.footer_nav a {
	color: hsl(357, 12%, 42%);
	font-size: 14px;
	margin: 0px 4px;
	text-decoration: none;
	font-weight: bold;
}

.made {
	margin-top: 10px;
	margin-bottom: 20px;
}
.rights {
	font-size: 12px;
	color: hsl(357, 8%, 60%);
}

.info-wrap {
	background: white;
	margin: 0 auto;
	height: 78px;
	width: 620px;
	left: 267px;
	top: 760px;
	border-radius: nullpx;
	    color: hsl(357, 50%, 22%);
}
.info-wrap h1 {
	margin: 40px 0px;
}
.info-wrap ol {
	-webkit-padding-start: 24px;
	        padding-inline-start: 24px;
}
.info-wrap ul {
	-webkit-padding-start: 24px;
